.container {
	max-width: 980px;
	width: 90%;
	margin: auto;
}

#monster-selector {
	label {
		display: block;
		margin-bottom: .5em;
	}
	
	select {
		margin-bottom: 1em;
		min-width: 100px;
	}
}

#monster-stats {
    text-align: center;
    $redColor: #922610;

    #stat-block {
        background: #FDF1DC;
        padding: 0 10px;
        box-shadow: 0 0 1.5em #867453;
        margin: 50px auto;
        display: inline-block;
        text-align: left;
        font-family: sans-serif;
        font-size: 14px;
        line-height: 1.2;

        &:before,
        &:after {
            content: '';
            display: block;
            background: #E69A28;
            border: 1px solid #000;
            height: 5px;
            padding: 0 10px 0;
            margin: 0 -10px;
        }

        .tapered-rule{
            display: block;
            width: 100%;
            height: 5px;
            border: none;
            color: $redColor;
            fill: $redColor;
            stroke: $redColor;
            margin-bottom: 10px;
        }

        h2 {
            font-family: serif;
            color: $redColor;
            font-size: 23px;
            line-height: 1.2em;
            margin: 10px 0 0;
            letter-spacing: 1px;
            font-variant: small-caps;
            font-weight: bold;
        }

        p {

            &#monster-type {
                font-weight: normal;
                font-style: italic;
                margin: 0 0 10px;
            }
            
            &.property {
                margin: 0;
                color: $redColor;
                font-weight: bold;

                span {
                    font-weight: normal;
                }
            }

            + .tapered-rule {
                margin-top: 10px;
            }
        }

        #ability-scores {
            display: flex;
            margin-bottom: 10px;
            p {
                text-align: center;
                margin: 0 8px;
                color: $redColor;

                &:first-child {
                    font-weight: bold;
                }
            }
        }
    }
}

